<template>
  <div class="sample">
    <p>
      {{ title }}
      <span v-if="playalong">Playalong: </span>
      <a v-if="showSample" :href="href">{{ name }}</a>
      <button @click="playPause">PLAY/PAUSE</button>
    </p>
  </div>
</template>

<script>
function getFileName(number) {
  return `CajonBasics${number.toLocaleString("de-AT", {
    minimumIntegerDigits: 2,
  })}.mp3`;
}

function getHref(sampleBaseUri, number) {
  if (number !== undefined) {
    return sampleBaseUri + getFileName(number);
  }
}

var audio;

export default {
  name: "Sample",
  props: {
    number: Number,
    title: String,
    playalong: Boolean,
  },
  data() {
    return {
      name: `Audio ${this.number}`,
      showSample: this.number !== undefined,
      href: getHref(this.sampleBaseUri, this.number),
    };
  },
  methods: {
    async playPause() {
      const audioFile = getHref(this.sampleBaseUri, this.number);
      console.log("play/pause " + audioFile);
      if (audio === undefined) {
        audio = new Audio(audioFile);
      }
      const isPlaying = audio.duration > 0 && !audio.paused;
      console.log(audio.duration);
      console.log(audio.paused);
      console.log(isPlaying);
      if (!isPlaying) {
        console.log(audio);
        await audio.play();
      } else {
        audio.pause();
      }
    },
  },
};
</script>

<style scoped>
div.sample {
  font-size: 1.25rem;
  margin: 0.5rem;
}
</style>