<template>
  <div class="page" v-bind:class="isEvenPage ? 'leftPage' : 'rightPage'">
    <h2>{{ title }}</h2>

    <sample
      v-for="sample in samples"
      :key="sample.name"
      v-bind="sample"
    ></sample>
    <p class="numbering">{{ number }}</p>
  </div>
</template>

<script>
import Sample from "./Sample.vue";

export default {
  name: "Page",
  components: { Sample },
  props: {
    number: Number,
    title: String,
    samples: Array,
  },
  data() {
    return {
      isEvenPage: this.number % 2 === 0,
    };
  },
};
</script>

<style scoped>
div.page {
  border: 1px solid gray;
  border-radius: 0.5rem;
  margin: 1rem auto 1rem auto;
  background-color: white;
  padding: 1rem;
  max-width: 24rem;
}
div.page.leftPage > p.numbering {
  text-align: left;
}
div.page.rightPage > p.numbering {
  text-align: right;
}
h2 {
  margin-top: 0;
  text-align: left;
}
p.numbering {
  margin: 0;
}
</style>